<template>
    <el-card>
        <el-form ref="form" label-width="80px">
            <el-row :gutter="40">
                <el-col :span="6">  
                    <el-form-item label="生产批号">
                        <el-input ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">  
                    <el-form-item label="生产单号">
                        <el-input ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">  
                    <el-form-item label="生产日期">
                        <el-input ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">  
                    <el-button type="primary">筛选</el-button>
                    <el-button type="primary" plain @click="dialogVisible=true">新增</el-button>
                    <el-button type="text">清空筛选</el-button>
                </el-col>
            </el-row>
            <el-table
            :header-cell-style="{background:'#f0f5ff'}"
            :data="tableData"
            style="width: 100%">
                <el-table-column
                    prop="date"
                    label="日期"
                    >
                </el-table-column>
                <el-table-column
                    prop="name"
                    label="姓名"  
                    >
                </el-table-column>
                <el-table-column
                    prop="address"
                    label="地址">
                </el-table-column>
                <el-table-column                  
                    label="操作">
                    <template >                     
                            <el-button type="primary"  size="mini">查看</el-button>                       
                            <el-button type="warning"  size="mini">编辑</el-button> 
                            <el-button type="danger"  size="mini">删除</el-button>                     
                    </template>
                </el-table-column>             
            </el-table>   
        </el-form>
        <el-pagination
                    :current-page="4"
                    :page-sizes="[100, 200, 300, 400]"
                    :page-size="100"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="400">
        </el-pagination>
        <!-- 关联销售订单对话框 -->
        <el-dialog
            title="请关联销售订单"
            :visible.sync="linkDialogVisible"
            width="50%"
        >
            <span style="color:#db001b;font-size:12px;">注：1、在左边框选择要关联的销售订单；2、在右边框选择该销售单下选择本次准备生产的产品。</span>
            <el-form ref="form" :model="form" label-width="80px" style="margin-top:10px;">
                <el-form-item label="活动名称" style="width:350px">
                    <el-input v-model="form.name" ><i slot="suffix" class="el-input__icon el-icon-search"></i></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="toLink">确认</el-button>
                <el-button @click="dialogVisible = false">取消</el-button>
            </span>
        </el-dialog>
        <!-- 生产类型选择对话框 -->
        <el-dialog
            title="请选择生产计划单类型"
            :visible.sync="dialogVisible"
            width="30%"
            >
            <el-row :gutter="30">
                <el-col :span="12" style="text-align:right;"><el-button type="primary" @click="showLinkDialog">关联销售订单</el-button></el-col>
                <el-col :span="12"><el-button type="primary" @click="toNotLink">不关联销售订单</el-button></el-col>
            </el-row>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
            </span>
        </el-dialog>
    </el-card>
</template>
<script>
export default {
    data(){
        return{
            form:{
                name:''
            },
            linkDialogVisible:false,
            dialogVisible:false,
            tableData: [{
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }, {
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1517 弄'
            }, {
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1519 弄'
            }, {
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1516 弄'
            }]
        }
    },
    methods:{
        toLink(){
            this.dialogVisible = false
            this.$router.push('/production/newLink')
        },
        toNotLink(){
            this.$router.push('/production/newNotLink')
        },
        showLinkDialog(){
            this.dialogVisible = false
            this.linkDialogVisible = true
        }
    }
}
</script>
<style lang="less" scoped>
.el-card {
  width: 100%;
  height: 99%;
  position: relative;
  .el-pagination{
      margin-top: 40px;
      margin-left: 200px;
  }
}
</style>


